body {
    background-color: #fff;
}
.filled {
    border: 2px outset #808080;
}
#root {
    display: flex;
    position: relative;
    width: 100%;
}
button, a {
    cursor: pointer;
}
.el-btn, .rs-play {
    color: #000;
    border: 2px outset #808080;
    border-radius: 5px;
    font-size: inherit;
    text-decoration: none;
    background-color: transparent;
}
.el-btn:disabled {
    pointer-events: none;
    opacity: .5;
}
.filled:active, #collapse:active+.filled, .pin-menu:active+.filled,
.el-btn:active, .rs-play:active {
    border-style: inset;
}